import React, { useState } from "react";
import {
  StyledWrapper,
  HamburgerWrapper,
  StyledSpan,
  MenuWrapper,
  MenuLink,
} from "./NavbarStyles";
import history from "../../Utils/history";
import { Link } from "react-router-dom";
export const Navbar = () => {
  const [isMenuActive, toggleMenu] = useState(false);
  const changeRoute = (url) => history.push(url);

  return (
    <>
      <MenuWrapper active={isMenuActive}>
        <MenuLink active={isMenuActive} onClick={() => changeRoute("/")}>
          <Link to="/" style={{ color: "inherit" }}>
            Home
          </Link>
        </MenuLink>
        <MenuLink
          active={isMenuActive}
          onClick={() => changeRoute("/favourites")}
        >
          <Link to="/favourites" style={{ color: "inherit" }}>
            Favourites
          </Link>
        </MenuLink>
      </MenuWrapper>
      <StyledWrapper>
        <HamburgerWrapper onClick={() => toggleMenu(!isMenuActive)}>
          <StyledSpan active={isMenuActive} />
          <StyledSpan active={isMenuActive} />
          <StyledSpan active={isMenuActive} />
        </HamburgerWrapper>
      </StyledWrapper>
    </>
  );
};
